.LayoutPays {
  position: relative;
  height: 20vmax;
  width: 30vmax;
  overflow:clip;
  top:0;
  margin-top: 20vmax;
}

.InfoPays{
  position:absolute;
  z-index: 1;
  font-size:1.5vmax;
  top:0;
  left:0;
  margin:0;
  padding: 0;
  width: 100%;
  height:100%;
  background: rgba(49, 45, 45, 0.7);
  color: rgba(255, 255, 255, 0.7);
  opacity: 1.2;
  align-items: center;
  justify-content: center;
}

li{
  list-style-type: none;
}

li:first-child {
  color: rgb(218, 144, 126);
  font-size:1.2rem;
  font-weight: bold;
}

ul{
  display:grid;
  text-align:center;
  padding:1px;
  margin: 1vmax;
  display: block-inline;
  flex-wrap: wrap;
  justify-content: center;
}


.Flag {
  z-index: -5;
  height: 20vmax;
  width: 30vmax;
  border-radius: 4px;
}

.LayoutPays :hover{
  transition:0.3s;
  transform: scale(1.2, 1.2);;
}